/* src/components/List.css */
.list-error {
  margin: 12px 0 18px;
  padding: 10px 12px;
  border: 1px solid var(--color-border);
  background: #fff;
  border-radius: 12px;
  color: var(--color-list-sub);
  font-size: 13px;
}

.list-grid {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 24px;
}

@media (max-width: 1024px) {
  .list-grid {
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 20px;
  }
}

@media (max-width: 768px) {
  .list-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 16px;
  }
}

@media (max-width: 480px) {
  .list-grid {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}

.list-card {
  display: block;
  text-decoration: none;
  color: inherit;
  background: #fff;
  border: 1px solid var(--color-border);
  border-radius: 12px;
  overflow: hidden;
  /*  GPU  hover   */
  will-change: transform;
  transform: translateZ(0);
  /*    */
  transition: transform 0.15s ease, border-color 0.15s ease;
}

.list-card:hover {
  transform: translateY(-2px) translateZ(0);
  border-color: rgba(58, 142, 135, 0.35);
}

/*   */
.list-card-img {
  position: relative;
  width: 100%;
  aspect-ratio: 16 / 10;
  overflow: hidden;
  background: var(--color-list-bg);
  /*    GPU  */
  transform: translateZ(0);
}

.list-card-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  /*     */
  image-rendering: -webkit-optimize-contrast;
}

/*    */
.list-heart-btn {
  position: absolute;
  left: 50%;
  top: 50%;
  /*  GPU  hover    */
  will-change: transform, opacity;
  transform: translate(-50%, -50%) translateY(6px) translateZ(0);

  width: 52px;
  height: 52px;
  border-radius: 50%;

  border: 1px solid rgba(0, 0, 0, 0.08);
  background: rgba(255, 255, 255, 0.92);

  display: inline-flex;
  align-items: center;
  justify-content: center;

  font-size: 22px;
  line-height: 1;
  color: #9aa0a6;

  opacity: 0;

  box-shadow: 0 14px 30px rgba(0, 0, 0, 0.14);
  transition: opacity 0.18s ease, transform 0.18s ease, color 0.18s ease;
}

.list-card:hover .list-heart-btn {
  opacity: 1;
  transform: translate(-50%, -50%) translateY(-1px) translateZ(0);
}

.list-heart-btn:active {
  transform: translate(-50%, -50%) scale(0.98) translateZ(0);
}

.list-heart-btn:focus-visible {
  outline: 2px solid var(--color-header-bg);
  outline-offset: 2px;
}

/*   */
.list-heart-btn.is-on {
  color: #e11d48;
  border-color: rgba(225, 29, 72, 0.22);
}

/*  FIX():   hover    ( hover    )
   -        
*/
.list-heart-btn.is-on {
  opacity: 1;
}

/*   */
.list-card-body {
  padding: 12px 12px 14px;

  /*        */
  position: relative;
  padding-bottom: 46px;
  /*     */
}

.list-item-title {
  margin: 0 0 8px;
  font-size: 14px;
  font-weight: 900;
  color: var(--color-list-title);
  line-height: 1.35;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.list-title {
  margin: 0 0 12px;
  font-size: 24px;
  font-weight: 900;
  color: var(--color-list-title);
}

.list-sub {
  margin: 0 0 8px;
  font-size: 12.5px;
  color: var(--color-list-sub);

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.list-date {
  margin: 0;
  font-size: 12px;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.45);
}

/*       */
.list-detail-btn {
  position: absolute;
  right: 12px;
  bottom: 12px;

  padding: 8px 10px;
  border-radius: 10px;
  border: 1px solid var(--color-border);
  background: #fff;
  font-size: 13px;
  cursor: pointer;
}

.list-detail-btn:hover {
  border-color: rgba(58, 142, 135, 0.35);
}

.list-detail-btn:focus-visible {
  outline: 2px solid rgba(58, 142, 135, 0.35);
  outline-offset: 2px;
}

/*     */
.observer-target {
  height: 20px;
  width: 100%;
}

/* Dots Loader */
.dots-loader {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 40px 20px;
}

.dots-loader-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: var(--color-header-bg, #3a8e87);
  animation: dots-loader-bounce 1.4s infinite ease-in-out both;
}

.dots-loader-dot:nth-child(1) {
  animation-delay: -0.32s;
}

.dots-loader-dot:nth-child(2) {
  animation-delay: -0.16s;
}

.dots-loader-dot:nth-child(3) {
  animation-delay: 0;
}

@keyframes dots-loader-bounce {

  0%,
  80%,
  100% {
    transform: scale(0);
    opacity: 0.5;
  }

  40% {
    transform: scale(1);
    opacity: 1;
  }
}