/* src/components/layout/EventSection.css */

/*      */
.home-event-section {
   padding: 24px 0 0;
}

/* :  +   */
.home-event-head {
   display: flex;
   justify-content: space-between;
   align-items: baseline;
   margin-bottom: 16px;
}

.home-event-eyebrow {
   font-size: 12px;
   color: var(--text-sub, #888);
   margin: 0 0 4px;
}

.home-event-title {
   margin: 0;
   font-size: 20px;
   font-weight: 800;
   letter-spacing: -0.02em;
   color: var(--text, #222);
}

.home-event-link {
   font-size: 13px;
   color: var(--text-sub, #666);
   text-decoration: none;
   padding: 6px 10px;
   border-radius: var(--r-pill, 999px);
   transition: background 0.15s ease, transform 0.08s ease;
}

.home-event-link:hover {
   background: rgba(0, 0, 0, 0.04);
   transform: translateY(-1px);
}

/*  :  3 */
.home-event-grid {
   display: grid;
   grid-template-columns: repeat(3, minmax(0, 1fr));
   gap: 16px;
   align-items: stretch;
}

/*     */
.home-event-card-link {
   text-decoration: none;
   color: inherit;
}

/*  =    */
.home-event-card {
   border-radius: 24px;
   overflow: hidden;
   background: transparent;
   box-shadow: var(--shadow-mid, 0 10px 24px rgba(0, 0, 0, 0.12));
   transition: transform 0.16s ease, box-shadow 0.16s ease;
}

/* hover  */
.home-event-card:hover {
   transform: translateY(-3px);
   box-shadow: var(--shadow-strong, 0 18px 40px rgba(0, 0, 0, 0.18));
}

/*  =  ,   */
.home-event-img {
   display: block;
   width: 100%;
   height: auto; /*     */
}

/*  */
@media (max-width: 1024px) {
   .home-event-grid {
      grid-template-columns: repeat(2, minmax(0, 1fr));
   }
}

@media (max-width: 720px) {
   .home-event-grid {
      grid-template-columns: repeat(1, minmax(0, 1fr));
   }
}
