/*   */
.support-tabs {
   display: flex;
   align-items: flex-end;
   justify-content: space-between;
   gap: 16px;
   margin: 18px 0 22px;
}

.support-tab-left {
   display: inline-flex;
   gap: 0;
   margin: 20px 0 18px;
   border-radius: 999px;
   padding: 3px;
   border: 1px solid var(--color-border);
   background: #ffffff;
   box-shadow: 0 8px 18px rgba(0, 0, 0, 0.08);
}

.support-tab-btn {
   appearance: none;
   border: 0;
   background: transparent;
   cursor: pointer;
   padding: 8px 12px;
   border-radius: 999px;
   font-size: 14px;
   font-weight: 800;
   color: var(--color-list-title, #333);
   transition: transform 0.08s ease, background 0.15s ease, color 0.15s ease;
}

.support-tab-btn:hover {
   background: rgba(58, 142, 135, 0.08);
}

.support-tab-btn:active {
   transform: scale(0.98);
}

.support-tab-btn.is-active {
   background: var(--color-header-bg, #3a8e87);
   color: var(--color-text, #fff);
}

/*     */
.support-tab-panel {
   margin-top: 8px;
}

/*    */
.support-section-title {
   margin: 0 0 14px;
   font-size: 20px;
   font-weight: 700;
   color: var(--color-list-title);
}

/* FAQ  */
.support-faq {
   margin-bottom: 24px;
}

.faq-list {
   border-radius: 16px;
   border: 1px solid var(--color-border);
   background: #ffffff;
   overflow: hidden;
   box-shadow: 0 10px 26px rgba(0, 0, 0, 0.1);
}

.faq-item+.faq-item {
   border-top: 1px solid var(--color-border);
}

.faq-question {
   width: 100%;
   padding: 12px 16px;
   border: none;
   background: transparent;
   display: flex;
   align-items: center;
   justify-content: space-between;
   cursor: pointer;
   font-size: 14px;
   text-align: left;
   color: var(--color-list-title);
   box-sizing: border-box;
}

.faq-item-active .faq-question {
   background: rgba(0, 124, 96, 0.04);
}

.faq-arrow {
   font-size: 12px;
   color: var(--color-list-sub);
   transition: transform 0.2s ease;
}

.faq-arrow.open {
   transform: rotate(180deg);
}

.faq-answer {
   padding: 8px 16px 14px;
   font-size: 13px;
   color: var(--color-list-sub);
   background: #ffffff;
}

/* FAQ     */
.support-info-inline {
   margin-top: 18px;
   padding-top: 14px;
   border-top: 1px solid var(--color-border);
}

.support-info-inline-title {
   margin: 0 0 8px;
   font-size: 14px;
   font-weight: 600;
   color: var(--color-list-title);
}

/* 1:1   */
.support-contact {
   margin-top: 8px;
   background: #ffffff;
   border-radius: 20px;
   border: 1px solid var(--color-border);
   padding: 22px 22px 24px;
   box-sizing: border-box;
   box-shadow: 0 14px 34px rgba(0, 0, 0, 0.16);
}

.support-contact-desc {
   margin: 0 0 16px;
   font-size: 13px;
   color: var(--color-list-sub);
}

.support-form {
   display: flex;
   flex-direction: column;
   gap: 14px;
}

.support-form-row {
   display: grid;
   grid-template-columns: repeat(2, minmax(0, 1fr));
   gap: 14px 18px;
}

.support-field {
   display: flex;
   flex-direction: column;
   gap: 6px;
}

.support-field label {
   font-size: 13px;
   font-weight: 500;
   color: var(--color-list-title);
}

.support-field input,
.support-field select,
.support-field textarea {
   border-radius: 10px;
   border: 1px solid var(--color-border);
   padding: 9px 11px;
   font-size: 14px;
   outline: none;
   box-sizing: border-box;
   background: #ffffff;
   transition: border-color 0.2s, box-shadow 0.2s, background-color 0.2s;
}

.support-field textarea {
   resize: vertical;
   min-height: 120px;
}

.support-field input:focus,
.support-field select:focus,
.support-field textarea:focus {
   border-color: var(--color-header-bg);
   box-shadow: 0 0 0 3px rgba(58, 142, 135, 0.16);
}

/*   */
.support-submit-btn {
   align-self: flex-end;
   margin-top: 6px;
   border-radius: 999px;
   border: none;
   padding: 10px 26px;
   font-size: 14px;
   font-weight: 600;
   cursor: pointer;
   background: var(--color-header-bg);
   color: var(--color-text);
   transition: background 0.15s, box-shadow 0.15s, transform 0.1s, opacity 0.1s;
}

.support-submit-btn:hover {
   opacity: 0.96;
   box-shadow: 0 8px 18px rgba(0, 0, 0, 0.22);
   transform: translateY(-1px);
}

.support-submit-btn:active {
   transform: translateY(0);
   box-shadow: none;
}

/*     (FAQ  ) */
.support-info-card {
   background: #ffffff;
   border-radius: 10px;
   border: 1px solid var(--color-border);
   padding: 12px 14px 12px;
   box-sizing: border-box;
   margin-top: 4px;
}

.support-info-text {
   margin: 0 0 8px;
   font-size: 13px;
   color: var(--color-list-sub);
}

.support-info-list {
   list-style: none;
   padding: 0;
   margin: 0 0 6px;
}

.support-info-list li {
   display: flex;
   gap: 8px;
   font-size: 13px;
   color: var(--color-list-title);
}

.support-info-list li+li {
   margin-top: 4px;
}

.support-info-label {
   min-width: 60px;
   font-weight: 600;
   color: var(--color-list-title);
}

.support-info-value {
   color: var(--color-list-sub);
}

.support-info-notice {
   margin: 2px 0 0;
   font-size: 12px;
   color: var(--color-list-sub);
}

/*  */
@media (max-width: 900px) {
   .support-tabs {
      display: flex;
      width: 100%;
      justify-content: center;
      flex-wrap: wrap;
   }
}

@media (max-width: 768px) {
   /* .support-inner { padding: 0 14px; }  pf-container  */

   .support-contact {
      padding: 18px 16px 20px;
      border-radius: 16px;
   }

   .support-form-row {
      grid-template-columns: 1fr;
   }

   .support-tabs {
      box-shadow: 0 6px 14px rgba(0, 0, 0, 0.12);
   }
}